/**
 * 
 */
import moment from 'moment'

export default {
  namespaced: true,
  state: {
    hasNew: false,
    errorLog: []
  },
  mutations: {
    /**
      * [setHasNew ]
     * @param  {[Boolean]} hasNew     [truefalse]
     */
    setHasNew (state, hasNew) {
      state.hasNew = hasNew
    },
    /**
     * [pushError ]
     * @param  {[Object]} error     []
     */
    pushError (state, error) {
      state.hasNew = true
      state.errorLog.unshift(Object.assign({}, error, { time: moment().format('HH:mm:ss') }))
    }
  }
}
